/* Styles for Step Zilla */
ol.progtrckr {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

ol.progtrckr li {
display: inline-block;
text-align: center;
line-height: 4.5rem;
cursor: pointer;
}

/* ol.progtrckr li span {
padding: 0 1.5rem;
} */

/* 650px */
@media (max-width: 834px) {
.progtrckr li span {
  display: none;
}
}
.progtrckr em {
display: none;
font-weight: 700;
padding-left: 1rem;
}

@media (max-width: 834px) {
.progtrckr em {
  display: inline;
}
}

ol.progtrckr li.progtrckr-todo {
color: silver;
border-bottom: 1px solid silver;
}

ol.progtrckr li.progtrckr-doing {
color: black;
border-bottom: 2px solid #CCCCCC;
}

ol.progtrckr li.progtrckr-done {
color: black;
border-bottom: 2px solid #5cb85c;
}

ol.progtrckr li:after {
content: "\00a0\00a0";
}

ol.progtrckr li:before {
position: relative;
bottom: -3.7rem;
float: left;
left: 50%;
}

ol.progtrckr li.progtrckr-todo:before {
content: "\039F";
color: silver;
background-color: white;
width: 1.2em;
line-height: 1.4em;
}

ol.progtrckr li.progtrckr-todo:hover:before {
color: #ff4500;
}

ol.progtrckr li.progtrckr-doing:before {
content: "\2022";
color: white;
background-color: #ff4500; /* #CCCCCC #545454 */
width: 1.2em;
line-height: 1.2em;
border-radius: 1.2em;
}

ol.progtrckr li.progtrckr-doing:hover:before {
color: #ff4500;
}

ol.progtrckr li.progtrckr-done:before {
content: "\2713";
color: white;
background-color: #5cb85c;
width: 1.2em;
line-height: 1.2em;
border-radius: 1.2em;
}

ol.progtrckr li.progtrckr-done:hover:before {
color: #333;
}


